import type {
	HttpExceptionResponse,
	IntoException
} from "@root/types/result.type.js"
import { Data } from "effect"

export class NoneError
	extends Data.TaggedError("None")
	implements IntoException
{
	public intoException(): HttpExceptionResponse {
		return {
			code: 500,
			msg: "none value"
		}
	}
}
